<template>
  <div
    class="mask"
    v-if="showCart"
    @click="handleCartShowChange"
  ></div>
  <div class="cart">
    <div class="product" v-if="showCart">
      <div class="product__header">
        <div
          class="product__header__all"
          @click="() => setCartItemsChecked(shopId)"
        >
          <span
            class="product__header__icon iconfont"
            v-html="calculations.allChecked ? '&#xe652;' : '&#xe615;'"
          >
          </span>
          <span class="product__header__all__text"> 全选</span>
        </div>
        <div class="product__header__clear">
          <span
            class="product__header__clear__btn"
            @click="() => cleanCartProducts(shopId)"
          >清空购物车</span>
        </div>
      </div>
      <template
        v-for="item in productList"
        :key=item._id
      >
        <div class="product__item">
          <div
            class="product__item__checked iconfont"
            v-html="item.check ? '&#xe652;' : '&#xe615;'"
            @click="changeCartItemCheck(shopId, item._id)"
          ></div>
          <img class="product__item__img" :src="item.imgUrl" />
          <div class="product__item__detail">
            <h4 class="product__item__title">{{ item.name }}</h4>
            <p class="product__item__price">
              <span class="product__item__yen">&yen;</span>{{ item.price }}
              <span class="product__item__origin">&yen;{{ item.oldPrice }}</span>
            </p>
          </div>
          <div class="product__number">
            <span
              class="product__number__minus"
              @click="() => { changeCartItemInfo(shopId, item._id, item, -1) }"
            >-</span>
            {{ item.count }}
            <span
              class="product__number__plus"
              @click="() =>{ changeCartItemInfo(shopId, item._id, item, 1) }"
            >+</span>
          </div>
        </div>
      </template>
    </div>
    <div class="check">
      <div class="check__icon">
        <img
          src="http://www.dell-lee.com/imgs/vue3/basket.png"
          class="check__icon__img"
          @click="handleCartShowChange"
        />
        <div class="check__icon__tag">{{ calculations.total }}</div>
      </div>
      <div class="check__info">
        总计：<span class="check__info__price">&yen; {{ calculations.price }}</span>
      </div>
      <div class="check__btn" @click="() => handleCheckClick(shopId)">
        去结算
      </div>
    </div>
  </div>
  <Toast v-if="show" :message="toastMessage"/>
</template>

<script>
import { ref } from 'vue'
import { useStore } from 'vuex'
import { useRoute, useRouter } from 'vue-router'
import { useCommonCartEffect } from '../../effects/cartEffect'
import Toast, { useToastEffect } from '../../components/Toast.vue'

const useCartEffect = (shopId, showCart) => {
  const { cartList, calculations, changeCartItemInfo, productList } = useCommonCartEffect(shopId)
  const { show, toastMessage, showToast } = useToastEffect()

  const store = useStore()
  const router = useRouter()

  const changeCartItemCheck = (shopId, productId) => {
    store.commit('changeCartItemCheck', { shopId, productId })
  }

  const cleanCartProducts = (shopId) => {
    store.commit('cleanCartProducts', { shopId })
    showCart.value = false
  }

  const setCartItemsChecked = (shopId) => {
    store.commit('setCartItemsChecked', { shopId })
  }

  const handleCheckClick = (shopId) => {
    if (calculations.value.total > 0) {
      router.push({ name: 'OrderConfirmationView', params: { shopId } })
    } else {
      showToast('请先选择商品')
    }
  }

  return {
    cartList, calculations, productList, changeCartItemInfo,
    changeCartItemCheck, cleanCartProducts, setCartItemsChecked,
    show, toastMessage, handleCheckClick
  }
}

const toggleCartEffect = () => {
  const showCart = ref(false)
  const handleCartShowChange = () => {
    showCart.value = !showCart.value
  }
  return { showCart, handleCartShowChange }
}

export default {
  components: { Toast },
  name: 'CartComponent',
  setup () {
    const route = useRoute()
    const shopId = route.params.id
    const { showCart, handleCartShowChange } = toggleCartEffect()
    const {
      calculations, productList, changeCartItemInfo,
      changeCartItemCheck, cleanCartProducts, setCartItemsChecked,
      show, toastMessage, handleCheckClick
    } = useCartEffect(shopId, showCart)
    return {
      shopId, productList, changeCartItemInfo,
      changeCartItemCheck, cleanCartProducts,
      calculations, setCartItemsChecked,
      showCart, handleCartShowChange,
      show, toastMessage, handleCheckClick
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/virables.scss';
@import '../../style/mixins.scss';
.mask{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, .5);
  z-index: 1;
}
.cart {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  background: $bgColor;
  z-index: 2;
}
.check {
  user-select: none;
  display: flex;
  height: .49rem;
  border-top: .01rem solid $content-bgColor;
  line-height: .49rem;
  &__icon {
    position: relative;
    width: .84rem;
    &__img {
      display: block;
      margin: .12rem auto;
      width: .28rem;
      height: .26rem;
      cursor: pointer;
      user-select: none;
    }
    &__tag {
      position: absolute;
      left: .46rem;
      top: .04rem;
      padding: 0 .04rem;
      min-width: .2rem;
      height: .2rem;
      line-height: .2rem;
      background-color: $hightlight-fontColor;
      border-radius: .1rem;
      font-size: .12rem;
      text-align: center;
      color: $bgColor;
      transform: scale(.5);
      transform-origin: left center;
    }
  }
  &__info {
    flex: 1;
    color: $content-fontColor;
    font-size: .12rem;
    &__price {
      line-height: .49rem;
      color: $hightlight-fontColor;
      font-size: .18rem;
    }
  }
  &__btn {
    width: .98rem;
    text-align: center;
    color: $bgColor;
    font-size: .14rem;
    cursor: pointer;
    background-color: #4FB0F9;
    &:hover {
      background-color: #4FA0F9;
    }
    a{
      color: $bgColor;
      text-decoration: none;
    }
  }
}
.product {
  overflow-y: scroll;
  flex: 1;
  background: $bgColor;
  user-select: none;
  &__header {
    display: flex;
    line-height: .52rem;
    border-bottom: .01rem solid $content-bgColor;
    font-size: .14rem;
    color: $content-fontColor;
    &__all {
      width: .64rem;
      margin-left: .16rem;
      &__text {
        vertical-align: center;
      }
    }
    &__icon {
      line-height: .2rem;
      display: inline-block;
      color: $btn-bgColor;
      font-size: .2rem;
      vertical-align: middle;
      cursor: pointer;
      user-select: none;
    }
    &__clear {
      flex: 1;
      margin-right: .16rem;
      text-align: right;
      &__btn {
        vertical-align: center;
        display: inline-block;
        color: $medium-fontColor;
        cursor: pointer;
      }
    }
  }
  &__item {
    position: relative;
    display: flex;
    padding: .12rem 0;
    margin: 0 .16rem;
    border-bottom: .01rem solid $content-bgColor;
    &__checked {
      display: inline-block;
      font-size: .2rem;
      line-height: .5rem;
      margin-right: .2rem;
      color: $btn-bgColor;
      cursor: pointer;
      user-select: none;
    }
    &__detail {
      overflow: hidden;
    }
    &__img {
      width: .46rem;
      height: .46rem;
      margin-right: .16rem;
    }
    &__title {
      margin: 0;
      line-height: .2rem;
      font-size: .14rem;
      color: $content-fontColor;
      @include ellipsis;
    }
    &__price {
      margin: .06rem 0 0 0;
      line-height: .2rem;
      font-size: .14rem;
      color: $hightlight-fontColor;
    }
    &__yen {
      font-size: .12rem;
    }
    &__origin {
      margin-left: .06rem;
      line-height: .2rem;
      font-size: .12rem;
      color: $light-fontColor;
      text-decoration: line-through;
    }
    .product__number {
      position: absolute;
      right: 0;
      bottom: .12rem;
      &__minus, &__plus
       {
        display: inline-block;
        width: .2rem;
        height: .2rem;
        line-height: .16rem;;
        border-radius: 50%;
        font-size: .2rem;
        text-align: center;
        cursor: pointer;
        user-select: none;
      }
      &__minus {
        border: .01rem solid $medium-fontColor;
        color: $medium-fontColor;
        margin-right: .05rem;
      }
      &__plus {
        background: $btn-bgColor;
        color: $bgColor;
        margin-left: .05rem;
      }
    }
  }
}
</style>
