<template>
  <div class="content">
    <div class="category">
      <div
        :class="{'category__item': true, 'category__item--active': currentTab === item.tab}"
        v-for="item in categories"
        :key="item.tab"
        @click=" () => handleTabClick(item.tab)"
      >{{item.name}}</div>
    </div>
    <div class="product">
      <div
        class="product__item"
        v-for="item in currentList"
        :key=item._id
      >
        <img class="product__item__img" :src="item.imgUrl" />
        <div class="product__item__detail">
          <h4 class="product__item__title">{{ item.name }}</h4>
          <p class="product__item__sales">月售{{ item.sales }}件</p>
          <p class="product__item__price">
            <span class="product__item__yen">&yen;</span>{{ item.price }}
            <span class="product__item__origin">&yen;{{ item.oldPrice }}</span>
          </p>
        </div>
        <div class="product__number">
          <span
            class="product__number__minus"
            @click="() => { changeCartItem(shopId, item._id, item, -1, shopName) }"
          >-</span>
          {{ cartList?.[shopId]?.productList?.[item._id]?.count || 0 }}
          <span
            class="product__number__plus"
            @click="() =>{ changeCartItem(shopId, item._id, item, 1, shopName) }"
          >+</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, ref, toRefs, watch } from 'vue'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { get } from '../../utils/request'
import { useCommonCartEffect } from '../../effects/cartEffect'

const categories = [
  { name: '全部商品', tab: 'all' },
  { name: '秒杀', tab: 'seckill' },
  { name: '新鲜水果', tab: 'fruit' }
]

// tab替换相关的逻辑
const useTabEffect = () => {
  const currentTab = ref(categories[0].tab)
  const handleTabClick = (tab) => {
    currentTab.value = tab
  }
  return { currentTab, handleTabClick }
}

// 全部商品相关的逻辑
const useContentListEffect = () => {
  const route = useRoute()
  const content = reactive({ contentList: [] })
  const shopId = route.params.id
  const getContentData = async () => {
    const result = await get(`/shop/${shopId}`)
    if (result?.errno === 0 && result?.data) {
      content.contentList = result.data.products
    }
  }
  const { contentList } = toRefs(content)
  return { contentList, getContentData, shopId }
}

// 分类商品相关的逻辑
const useCurrentListEffect = (contentList, currentTab) => {
  const current = reactive({ currentList: [] })

  watch([currentTab, contentList], ([newTab, newContentList]) => {
    current.currentList = []

    for (let i = 0; i < newContentList.length; i++) {
      if (newTab === 'all' || newContentList[i].categories === newTab) {
        current.currentList.push(newContentList[i])
      }
    }
  })

  const { currentList } = toRefs(current)
  return { currentList }
}

export default ({
  name: 'ContentComponent',
  props: ['shopName'],
  setup () {
    const { currentTab, handleTabClick } = useTabEffect()
    const { contentList, getContentData, shopId } = useContentListEffect()

    getContentData()

    const { currentList } = useCurrentListEffect(contentList, currentTab)
    const { changeCartItemInfo, cartList } = useCommonCartEffect()

    const store = useStore()
    const changeShopName = (shopId, shopName) => {
      store.commit('changeShopName', { shopId, shopName })
    }
    const changeCartItem = (shopId, productId, product, num, shopName) => {
      changeCartItemInfo(shopId, productId, product, num)
      changeShopName(shopId, shopName)
    }

    return {
      categories, handleTabClick, currentTab, currentList,
      shopId, changeCartItem, cartList
    }
  }
})
</script>

<style lang="scss" scoped>
@import '../../style/virables.scss';
@import '../../style/mixins.scss';
.content {
  display: flex;
  position: absolute;
  left: 0;
  right: 0;
  top: 1.5rem;
  bottom: .5rem;
}
.category {
  overflow-y: scroll;
  height: 100%;
  width: .76rem;
  background: $search-bgColor;
  &__item {
    line-height: .4rem;
    text-align: center;
    font-size: .14rem;
    color: #333;
    cursor: pointer;
    user-select: none;
    &--active {
      background: $bgColor;
    }
  }
}
.product {
  overflow-y: scroll;
  flex: 1;
  &__item {
    position: relative;
    display: flex;
    padding: .12rem 0;
    margin: 0 .16rem;
    border-bottom: .01rem solid $content-bgColor;
    &__detail {
      overflow: hidden;
    }
    &__img {
      width: .68rem;
      height: .68rem;
      margin-right: .16rem;
    }
    &__title {
      margin: 0;
      line-height: .2rem;
      font-size: .14rem;
      color: $content-fontColor;
      @include ellipsis;
    }
    &__sales {
      margin: .06rem 0;
      font-size: .12rem;
      color: $content-fontColor;
    }
    &__price {
      margin: 0;
      line-height: .2rem;
      font-size: .14rem;
      color: $hightlight-fontColor;
    }
    &__yen {
      font-size: .12rem;
    }
    &__origin {
      margin-left: .06rem;
      line-height: .2rem;
      font-size: .12rem;
      color: $light-fontColor;
      text-decoration: line-through;
    }
    .product__number {
      position: absolute;
      right: 0;
      bottom: .12rem;
      &__minus, &__plus
       {
        display: inline-block;
        width: .2rem;
        height: .2rem;
        line-height: .16rem;;
        border-radius: 50%;
        font-size: .2rem;
        text-align: center;
        cursor: pointer;
        user-select: none;
      }
      &__minus {
        border: .01rem solid $medium-fontColor;
        color: $medium-fontColor;
        margin-right: .05rem;
      }
      &__plus {
        background: $btn-bgColor;
        color: $bgColor;
        margin-left: .05rem;
      }
    }
  }
}
</style>
